import './face-recognition.css';

const FaceRecognition = ({imageUrl, boxs}) => {
  return (<div className="center ma">
      <div className="absolute mt2">
      <img id="inputimage" src={imageUrl} alt="result" width="500px" height="auto"/>
      {boxs.map((box,i )=> (<div key={i} className="bounding-box" style={{top:box.topRow, right: box.rightCol, bottom: box.bottomRow, left: box.leftCol}}></div>))}
      
      </div>
      
  </div>);
};

export default FaceRecognition;


